.card {
  margin: 0 2% !important ;
}

.infected {
  border-bottom: 10px solid rgba(132, 0, 255, 0.7);
}
.recovered {
  border-bottom: 10px solid rgba(0, 255, 85, 0.7);
}
.deaths {
  border-bottom: 10px solid rgba(255, 0, 0, 0.7);
}

@media screen and (max-width: 1000px) {
  .card {
    margin: 2% 2% !important;
  }
}
